.none{
  display: none;
}
.block{
  display:block;
}
/*  */

.project_title h1{
  font: 2em;
  text-align: center;
  margin: 50px 0px;
  margin-bottom: 200px;
}
.project_container{
  margin:0px 20px;
  /* height: 70vh; */
  display: flex;
  align-items: top;
}
.container_img{
  flex: 1;
  text-align: center;
  position: relative;
}
.img_btn{
  position:absolute;
  opacity: 0.4;
  
}
.container_img :first-child{
  top:50%;
  left: 0%;
}
.container_img :nth-child(2){
  top: 50%;
  right: 0%;
}
.container_img img{
  width: 80%;
  /* object-fit: cover; */
}
.container_desc{
  flex: 1;
  font-size: 1.3em;
}
.container_desc p:nth-child(2){
  margin-bottom:30px;
  border-bottom: 1px solid black;
}
.container_desc p:nth-child(n+3){
  margin-bottom:10px;
}

@media screen and (max-width:900px) {
  .project_container{
    display: block;
  }
  .container_desc p:first-child{
    margin-top: 80px;
  }

  .project_title h1{
    margin: 50px 0px;
  }
  
}
